<template lang="pug">
.custom_dialog
  .dialog_container
    .dialog_close_btn(@click="closeClick")
      img(src="@/assets/img/dialog/close.svg")
    h2.dialog_title {{ dialogTitle[this.$store.state.typeCustomDialogInput] }}
    .dialog_content
      p {{ dialogContent1[this.$store.state.typeCustomDialogInput] }}
      p {{ dialogContent2[this.$store.state.typeCustomDialogInput] }}
    input#email.dialog_input(
      type="email",
      name="email",
      placeholder="輸入 email",
      v-model="inputValue.trackEmail"
    )
    .dialog_btn.follow_btn.flex_h(
      :class="{ is_reserve: this.$store.state.typeCustomDialogInput === 0 }",
      @click="btnClick"
    )
      p {{ dialogBtn[this.$store.state.typeCustomDialogInput] }}
      img(src="@/assets/img/dialog/arrow_right.svg")
</template>

<script>
import { trackEmail } from "@/api/email_track";

export default {
  name: "CustomDialogInput",
  data() {
    return {
      dialogTitle: ["掌握預約投保進度", "追蹤預約投保活動"],
      dialogContent1: ["取得最新活動進度，", "對寵物險商品有興趣，"],
      dialogContent2: [
        "可即時掌握寵物險預約投保活動。",
        "也想追蹤下一檔預約投保活動。",
      ],
      dialogBtn: ["前往預約投保", "追蹤"],
      inputValue: {
        trackEmail: "",
      },
    };
  },
  methods: {
    closeClick() {
      this.$store.commit("setShowCustomDialogStatus", false);
    },
    btnClick() {
      if (this.$store.state.typeCustomDialogInput === 1) {
        console.log("track");
        trackEmail(this.inputValue).then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            alert(response.data.data);
            this.$store.commit("setShowCustomDialogStatus", false);
          } else {
            alert(response.data.message);
          }
        });
      } else {
        this.$router.push({ name: "applicationNotice" });
        this.$store.commit("setShowCustomDialogStatus", false);
        console.log("reserve");
      }
    },
  },
};
</script>

