<template lang="pug">
nav(:class="{ is_leave: isLeave }", v-resize="onResize")
  .nav_top_bar
  .nav_main.flex_h
    .nav_logo
      img.logo(
        :srcset="`${imgs.logoFullImg} 1x,${imgs.logoFull2xImg} 1.5x,${imgs.logoFull2xImg} 2x`",
        :src="imgs.logoFullImg"
      )
  .nav_bottom_bar
    img(src="@/assets/img/menu/golden-line@2x.png")
</template>

<script>
import "@/assets/style/main.scss";
import LogoFullImg from "@/assets/img/menu/logo.png";
import LogoFull2xImg from "@/assets/img/menu/logo@2x.png";

export default {
  data() {
    return {
      fullWidth: 0,
      isLeave: false,
      imgs: {
        logoFullImg: LogoFullImg,
        logoFull2xImg: LogoFull2xImg,
      },
    };
  },
  methods: {
    topAction() {
      window.addEventListener("scroll", () => {
        if (window.innerWidth >= 991) {
          if (this.$store.state.actionTopMargin <= 95 && this.$router.currentRoute.name=='home') {
            this.isLeave = true;
          } else {
            this.isLeave = false;
          }
        }
      });
    },
    onResize({ width }) {
      this.checkScreenWidth(width);
    },
    checkScreenWidth(width) {
      if (width > 991) {
        if (this.$store.state.actionTopMargin <= 95 && this.$router.currentRoute.name=='home') {
          if (!this.isLeave) {
            this.isLeave = true;
          }
        } else {
          if (this.isLeave) {
            this.isLeave = false;
          }
        }
      } else {
        if (this.isLeave) {
          this.isLeave = false;
        }
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.topAction();
    });
  },
  activated() {
    this.$nextTick(() => {
      if (window.innerWidth >= 991) {
        if (this.$store.state.actionTopMargin <= 95) {
          this.isLeave = true;
        } else {
          this.isLeave = false;
        }
      }

      this.checkScreenWidth(this.$el.offsetWidth);
    });
  },
};
</script>

