import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: () => import("@/views/home/index"),
  },
  {
    path: "/applicationNotice",
    name: "applicationNotice",
    component: () => import("@/views/application_notice/index"),
  },
  {
    path: "/petInfo",
    name: "petInfo",
    component: () => import("@/views/pet_info/index"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
