import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    actionTopMargin: 0,
    showCustomDialogInput: false,
    typeCustomDialogInput: 0
  },
  mutations: {
    setActionTopMargin(state, int) {
      state.actionTopMargin = int;
    },
    setShowCustomDialogStatus(state, boolean) {
      state.showCustomDialogInput = boolean;
    },
    setTypeCustomDialog(state, int) {
      state.typeCustomDialogInput = int;
    },
  },
  actions: {},
  getters: {},
  modules: {},
});
